@font-face {
  font-family: Yekan;
  src: url(/static/media/iranyekanwebregular.e31c09af.1fdcadec.woff2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Yekan', sans-serif;
  -webkit-user-select: none;
          user-select: none;
  text-decoration: none;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  min-height: 100vh;
  background: #eee;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #005dc7;
  border-radius: 50px;
  width: 5px;
}
.details_container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 90px 16px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}

.details_linkBar {
  width: 100%;
  max-width: 800px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 5px 20px;
  padding: 15px 25px;
  border-right: 5px solid #8fc700;
}

.details_backLink {
  transition: all 0.3s ease;
  color: rgba(0, 0, 0, 0.5);
  display: inline-flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  cursor: pointer;
}

.details_backLink:hover {
  color: #7fb002;
  padding-right: 5px;
}

.datails_card {
  width: 100%;
  max-width: 800px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 20px;
  border-left: 5px solid #8fc700;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  grid-gap: 30px;
  gap: 30px;
  padding: 30px 25px;
  transition: all 0.3s ease;
  overflow: hidden;
}

.image_box {
  width: 100%;
  max-width: 450px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 20px;
  border-right: 5px solid #8fc700;
  padding: 15px;
  position: relative;
}

.kg_image {
  width: 60px;
  position: absolute;
  bottom: -10px;
  left: -10px;
}

.card_image {
  width: 260px;
}

.main_content_box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.card_category {
  color: rgba(0, 0, 0, 0.5);
  border-right: 3px solid #8fc700;
  padding-right: 5px;
}

.card_title,
.card_price {
  font-size: 20px;
}

.card_content {
  width: 100%;
  margin-top: 20px;
  color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.card_information {
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  color: rgba(0, 0, 0, 0.7);
  font-size: 15px;
}

.card_buy {
  display: block;
  margin-top: 30px;
  width: 100%;
  height: 45px;
  line-height: 35px;
  display: block;
  border-radius: 5px;
  background-color: #8fc700;
  border: none;
  outline: none;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.card_buy:hover {
  background-color: #7fb002;
}

@media screen and (min-width: 670px) {
  .datails_card {
    flex-direction: row;
  }
}

.basket_buttons {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
}

.basket_plus,
.basket_minus {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  cursor: pointer;
}

.basket_plus {
  background-color: #8fc700;
}

.counter_number {
  width: 35px;
  height: 50px;
  display: flex;
  border: 2px solid #8fc700;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.basket_minus {
  background-color: tomato;
  color: #fff;
}

.basket_plus:hover,
.basket_minus:hover {
  opacity: 0.8;
}

.favorite-wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
}

.favorite_removeAll {
  width: 200px;
  height: 35px;
  line-height: 35px;
  display: block;
  margin-top: 15px;
  border-radius: 5px;
  background-color: tomato;
  border: none;
  outline: none;
  color: #fff;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.favorite_removeAll:hover {
  background-color: rgb(79, 58, 54);
}

.favorite_container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  gap: 10px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.favorite_card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 5px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 15px 10px 10px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.favorite_card:hover {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 10px 30px;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

.favorite_img {
  width: 90%;
}

.favorite_content {
  width: 100%;
  color: rgba(0, 0, 0, 0.7);
}

.favorite_title {
  width: 100%;
  text-align: center;
  margin-top: 10px;
}

.favorite_button {
  width: 100%;
  height: 35px;
  line-height: 35px;
  display: block;
  margin-top: 15px;
  border-radius: 5px;
  background-color: tomato;
  border: none;
  outline: none;
  color: #fff;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.favorite_button:hover {
  background-color: rgb(245, 40, 4);
}

.favorite_empty {
  position: absolute;
  top: 65%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
}

.favorite_empty_title {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.6);
}

.favorite_empty_img {
  width: 350px;
}

.favorite_container_linkBar {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 70px 16px 0;
}

.favorite_linkBar {
  width: 100%;
  padding: 15px 16px;
  margin-top: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 5px 20px;
  border-right: 5px solid #8fc700;
}

.favorite_linkBar span {
  color: rgb(245, 40, 4);
}

.favorite_backLink {
  transition: all 0.3s ease;
  color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.favorite_backLink:hover {
  color: #ff3939;
  padding-left: 5px;
}

.header {
  width: 100%;
  background-color: #494e3a;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
}

.nav {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 25px;
}

.icon_Sopping_box {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.signup {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  color: #fff;
  font-size: 20px;
  transition: all 0.3s;
}

.shoppe_icon_box,
.mark_icon_box {
  position: relative;
}

.shoppe_icon_box:hover .shop_icon {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

.mark_icon_box:hover .mark_icon {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

.shop_icon {
  color: #fff;
  font-size: 40px;
  transition: all 0.3s;
}

.mark_icon {
  color: #fff;
  font-size: 30px;
  transition: all 0.3s;
}

.badge_shope,
.badge_mark {
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: -3px;
  bottom: 0;
  border-radius: 20%;
  background-color: tomato;
  font-size: 15px;
  color: #fff;
}

.badge_mark {
  right: -5px;
  bottom: -5px;
}

.logo {
  color: #fff;
  font-size: 20px;
}

@-webkit-keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
    opacity: 0;
  }

  10%,
  20% {
    -webkit-transform: scale3d(0.8, 0.66, 0.66) rotate3d(0, 0, 1, -3deg);
            transform: scale3d(0.8, 0.66, 0.66) rotate3d(0, 0, 1, -3deg);
  }

  30%,
  50%,
  70%,
  90% {
    -webkit-transform: scale3d(1.2, 1.2, 1.2) rotate3d(0, 0, 1, 3deg);
            transform: scale3d(1.2, 1.2, 1.2) rotate3d(0, 0, 1, 3deg);
  }

  40%,
  60%,
  80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
            transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
    opacity: 1;
  }
}

@keyframes tada {
  from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
    opacity: 0;
  }

  10%,
  20% {
    -webkit-transform: scale3d(0.8, 0.66, 0.66) rotate3d(0, 0, 1, -3deg);
            transform: scale3d(0.8, 0.66, 0.66) rotate3d(0, 0, 1, -3deg);
  }

  30%,
  50%,
  70%,
  90% {
    -webkit-transform: scale3d(1.2, 1.2, 1.2) rotate3d(0, 0, 1, 3deg);
            transform: scale3d(1.2, 1.2, 1.2) rotate3d(0, 0, 1, 3deg);
  }

  40%,
  60%,
  80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
            transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
    opacity: 1;
  }
}

.tada {
  -webkit-animation-name: tada;
          animation-name: tada;
  -webkit-animation-duration: 300ms;
          animation-duration: 300ms;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

@media screen and (max-width: 660px) {
  .search_header {
    display: none;
  }
}

.product_container {
  width: 100%;
  max-width: 1200px;
  min-height: 450px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  gap: 10px;
  grid-row-gap: 20px;
  row-gap: 20px;
  padding: 16px;
}

.box {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 5px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 15px 10px 10px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  max-height: 350px;
}

.box:hover {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 10px 30px;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

.product_img {
  width: 100%;
}

.content {
  width: 100%;
  color: rgba(0, 0, 0, 0.7);
}

.title {
  width: 100%;
  margin-top: 10px;
}

.price {
  width: 100%;
  text-align: left;
  margin-top: 5px;
}

.price span {
  font-size: 15px;
}

.products_button {
  width: 100%;
  height: 35px;
  line-height: 35px;
  display: block;
  margin-top: 15px;
  border-radius: 5px;
  border: none;
  outline: none;
  color: rgb(34, 32, 32);
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.buy_button {
  background-color: #8fc700;
}

.add_button {
  background-color: #ffaa00;
}

.buy_button:hover {
  background-color: #7fb002;
}

.add_button:hover {
  background-color: #ff8000;
}

.buy_icon {
  font-size: 18px;
}

.Interest {
  width: 25px;
  height: 25px;
  background-color: #eee;
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.2) 3px 3px 5px;
  cursor: pointer;
  transition: all 0.3s;
  z-index: 10;
}

.heart_Fill,
.heart {
  color: #f00;
}

.not_products {
  position: absolute;
  top: 65%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.products_empty_title {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.6);
  text-align: center;
}

.products_empty_img {
  width: 230px;
  opacity: 0.6;
}

.products_empty_guide {
  font-size: 16px;
  color: tomato;
  text-align: center;
}

.search_Container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  margin-top: 16px;
  display: flex;
  align-items: center;
}

.search_box {
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 5px 20px;
}

/* Media  */

@media screen and (min-width: 500px) {
  .favorite_container,
  .product_container {
    grid-template-columns: repeat(3, 1fr);
  }

  .basket_img img {
    width: 110px;
  }

  .basket_buttons {
    grid-gap: 10px;
    gap: 10px;
  }

  .counter_number {
    width: 40px;
  }

  .basket_title {
    font-size: 18px;
  }

  .basket_content,
  .basket_counter {
    padding: 10px 15px;
  }
}

@media screen and (min-width: 660px) {
  .search_Container {
    display: none;
  }
}

@media screen and (min-width: 800px) {
  .favorite_container,
  .product_container {
    grid-template-columns: repeat(4, 1fr);
  }

  .basket_container {
    flex-direction: row;
  }

  .basket_itemBox {
    width: 60%;
  }

  .basket_priceBox {
    width: 40%;
  }
}

@media screen and (min-width: 1000px) {
  .favorite_container,
  .product_container {
    grid-template-columns: repeat(5, 1fr);
  }
}

.filter_container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 70px 16px 5px;
    margin-top: 16px;
}

.filter_btnBox {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px 5px;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 5px 20px;
    overflow: auto;
}

.filter_btnBox::-webkit-scrollbar {
    height: 3px;
}

.filter_btnBox::-webkit-scrollbar-thumb {
    background-color: #8fc700b6;
    border-radius: 3px;
}

.filter_icon {
    width: 80px;
    color: #8fc700;
    padding: 0;
}

.filter_btn {
    height: 35px;
    line-height: 35px;
    display: block;
    border-radius: 25px;
    background-color: #4b417f;
    border: none;
    outline: none;
    color: #fff;
    font-size: 15px;
    cursor: pointer;
    transition: all .3s ease;
    padding: 0 20px;
}

.filter_btn:hover {
    background-color: #7fb002;
}
